.calendar.scroll {
  position: relative;
  padding-top: 30px; }
  .calendar.scroll .calendar-weekday-header-row {
    position: absolute;
    left: 16px;
    top: 0px; }
  .calendar.scroll .scroll-box {
    overflow-y: scroll;
    height: 380px; }
  .calendar.scroll .calendar-month-grid {
    position: relative;
    -ms-flex-direction: column;
        flex-direction: column; }
  .calendar.scroll .calendar-month {
    padding-top: 0px; }
  .calendar.scroll .calendar-month-title {
    z-index: 2;
    background: white;
    position: relative; }



